<template>
    <div id="app">
        <div id="imgWrap">
            <img src="~../assets/girl.jpg" alt="girl" id="profilePicture">
            <div id="titleForProfileImg"><p>{{dataValue.name}}</p> </div>
        </div>
        <p id="date">  {{dataValue.addDate}}  </p>
        <section id="mainInfo">
            <p>{{dataValue.disease}}</p>
            <p><b></b> <br />
                <span id="heartFull">&hearts;</span>
                <span id="heartFull">&hearts;</span>
                <span id="heartFull">&hearts;</span>
                <span id="heartMiddle">&hearts;</span>
                <span id="heartMiddle">&hearts;</span>
                <span id="heartMiddle">&hearts;</span>
                <span id="heartMiddle">&hearts;</span>
                <span id="heartMiddle">&hearts;</span>
                <span id="heartEmpty">&hearts;</span>
                <span id="heartEmpty">&hearts;</span>
                <span id="heartEmpty">&hearts;</span>
            </p>
            <p><b>:</b> <br />
                {{dataValue.age}} 
            </p>

            <p><b> :</b><br />
                {{dataValue.bloodGroup}} <br />
                {{dataValue.bloodType}}
            </p>
            <p><b> :</b><br />
                {{dataValue.place}} <br />
                {{dataValue.adress}}
            </p>
            <p>       ,     
                .            ,
                ,     .      
                 5   .
            </p>
            <p>
                         :
            <p id="redLine" v-model="dataValue.redLine">
                "  2016        .   2017      .     .        .           .   .       2017           ."
            </p>
            </p>

            <i v-model="dataValue.diseaseDescription"> <b> </b> (. Myeloma endotheliale)    .  ,  ,      , , , ,   .</i>
            <footer>
                <ul>
                    <li><a href="#"><img src="~../assets/32/facebook.png" alt=""></a></li>
                    <li><a href="#"><img src="~../assets/32/skype.png" alt=""></a></li>
                    <li><a href="#"><img src="~../assets/32/twitter.png" alt=""></a></li>
                </ul>
            </footer>
        </section>

    </div>
</template>

<script>
    export default {
        data: function () {
            return {
                dataValue: {
                    name: ' ',
                    addDate: '17.05.2017',
                    disease:' ',
                    age:'8',
                    bloodGroup:' A II Rh + ',
                    bloodType:' ',
                    place:'  ',
                    adress:', ., 33/43,  ',
                    redLine:'',
                    diseaseDescription:''
                }
            }
        } //data

    }
</script>

<style scoped>


    #profilePicture{
        width: 100%;
    }
    #titleForProfileImg{
        padding: 5px;
        background-color: white;
        opacity: 0.7;
        position: absolute;
        bottom: 12px;
        overflow: auto;
        margin: auto;
        right: 20%; left: 20%;
        display:flex;
        justify-content:center;
    }
    #titleForProfileImg p{
        opacity: 1 !important;
        color: red;
        font-size: 16px;
    }
    #imgWrap{
        position: relative;
        /*display: flex;*/
        /*align-items: center;*/
        /*justify-content: center;*/
    }
    #date{
        text-align: right;
        margin-right: 10px;
        color: #95989A;
    }
    #heartFull{
        color: #EE2412; font-size: 45px; font-family: 'Comic Sans MS'; opacity: 1
    }
    #heartMiddle{
        color: #EE2412; font-size: 45px; font-family: 'Comic Sans MS'; opacity: 0.5
    }
    #heartEmpty{
        color: #EE2412; font-size: 45px; font-family: 'Comic Sans MS'; opacity: 0.1;
        border-color: red;
    }
    #redLine{
        border: 2px solid red;
        padding: 20px;
        width: auto;
    }
    footer ul li{
        list-style-type: none; /*   */
        display: inline;
        padding: 10px;
    }
    footer ul li:first-child{
        margin-left: -40px;
    }

    footer{
        display: flex;
        justify-content: center;
    }
    #mainInfo{
        border-top: 1px solid rgba(0, 0, 0, 0.42);
        padding:12px;
    }
    b{
        font-size: 110%;
    }
</style>



